/* =======================
   VARIÁVEIS GLOBAIS
   ======================= */
:global(:root) {
  --bg-light: #f5f5f5;
  --sidebar-light: #fff;
  --chat-light: #f0f0f0;
  --sent-light: #0b93f6;
  --received-light: #e5e5ea;
  --text-light: #000;
  --border-light: #ccc;

  --bg-dark: #121212;
  --sidebar-dark: #1f1f1f;
  --chat-dark: #181818;
  --sent-dark: #0b93f6;
  --received-dark: #3e3e3e;
  --text-dark: #f5f5f5;
  --border-dark: #333;

  --accent-color: #007bff;
}

/* =======================
   RESET GLOBAL
   ======================= */
:global(*) {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:global(body) {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: Arial, sans-serif;
}

/* =======================
   CONTAINER PRINCIPAL
   ======================= */
.container {
  display: flex;
  height: 100%;
  width: 100%;
  background-color: var(--bg-light);
  color: var(--text-light);
}

.darkTheme {
  background-color: var(--bg-dark);
  color: var(--text-dark);
}

/* =======================
   SIDEBAR
   ======================= */
.sidebar {
  width: 250px;
  display: flex;
  flex-direction: column;
  background-color: var(--sidebar-light);
  border-right: 1px solid var(--border-light);
  height: 100%;
  padding: 10px;
}

.darkTheme .sidebar {
  background-color: var(--sidebar-dark);
  border-color: var(--border-dark);
}

/* Lista de usuários */
.userList {
  list-style: none;
  padding: 0;
  margin: 0;
  flex: 1;
  overflow-y: auto;
}

.userList li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 10px;
  border-radius: 8px;
  margin-bottom: 5px;
  cursor: pointer;
  transition: background 0.2s;
}

.userList li:hover {
  background-color: #e5e5ea;
}

.darkTheme .userList li:hover {
  background-color: #333;
}

.userList li.selected {
  background-color: var(--accent-color);
  color: white;
}

.contactPhoto {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}

/* =======================
   MENU DOS 3 PONTINHOS
   ======================= */
.userOptionsContainer {
  position: relative;
  display: inline-block;
}

.userDots {
  cursor: pointer;
  font-size: 20px;
  padding: 5px;
  border-radius: 50%;
  transition: background 0.2s;
}

.userDots:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Pop-up */
.userButtonsPopup {
  position: absolute;
  top: 30px;
  right: 0;
  background-color: var(--popup-bg, #fff);
  color: var(--popup-text, #000);
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  z-index: 999;
  min-width: 180px;
  overflow: hidden;
  animation: fadeIn 0.15s ease;
}

.userButtonsPopup button {
  display: block;
  width: 100%;
  padding: 10px;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.2s;
}

.userButtonsPopup button:hover {
  background: rgba(0, 0, 0, 0.1);
}

/* Tema escuro */
.darkTheme .userButtonsPopup {
  background-color: #222 !important;
  border: 1px solid #444;
  color: white !important;
}

.darkTheme .userButtonsPopup button {
  color: white !important;
}

.darkTheme .userButtonsPopup button:hover {
  background-color: #333 !important;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

/* =======================
   BOTÕES INFERIORES
   ======================= */
.sidebarButtons {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

.sidebarButtons button {
  border: none;
  border-radius: 25px;
  padding: 10px 15px;
  cursor: pointer;
  font-weight: bold;
  background-color: var(--accent-color);
  color: #fff;
  transition: background 0.2s;
}

.sidebarButtons button:hover {
  background-color: #005bb5;
}

/* =======================
   ÁREA PRINCIPAL (CHAT)
   ======================= */
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Cabeçalho da conversa */
.chatHeader {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  border-bottom: 1px solid var(--border-light);
  background-color: var(--chat-light);
}

.darkTheme .chatHeader {
  background-color: var(--chat-dark);
  border-color: var(--border-dark);
}

.chatHeader img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.chatHeader .contactName {
  font-weight: bold;
  font-size: 18px;
}

/* Mensagens */
.messages {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 15px 20px;
  overflow-y: auto;
  background-color: var(--chat-light);
  font-size: var(--msg-font-size, 16px);
}

.darkTheme .messages {
  background-color: var(--chat-dark);
  color: var(--text-dark);
}

.message {
  padding: 10px 15px;
  border-radius: 20px;
  max-width: 70%;
  word-wrap: break-word;
  line-height: 1.4;
  margin: 4px 0;
  position: relative;
}

.sent {
  align-self: flex-end;
  background-color: var(--sent-light);
  color: white;
}

.darkTheme .sent {
  background-color: var(--sent-dark);
}

.received {
  align-self: flex-start;
  background-color: var(--received-light);
  color: black;
}

.darkTheme .received {
  background-color: var(--received-dark);
  color: var(--text-dark);
}

.messageTime {
  position: absolute;
  bottom: -15px;
  right: 10px;
  font-size: 10px;
  color: #999;
}

.darkTheme .messageTime {
  color: #ccc;
}

/* Input */
.inputContainer {
  display: flex;
  gap: 10px;
  padding: 10px 15px;
  border-top: 1px solid var(--border-light);
  background-color: var(--sidebar-light);
}

.darkTheme .inputContainer {
  background-color: var(--sidebar-dark);
  border-color: var(--border-dark);
}

.inputContainer input {
  flex: 1;
  padding: 10px 15px;
  border-radius: 25px;
  border: 1px solid var(--border-light);
  outline: none;
  background-color: #fff;
  color: #000;
}

.darkTheme .inputContainer input {
  border-color: var(--border-dark);
  background-color: #121212;
  color: #f5f5f5;
}

.inputContainer button {
  padding: 10px 20px;
  border-radius: 25px;
  border: none;
  background-color: var(--accent-color);
  color: #fff;
  cursor: pointer;
  font-weight: bold;
}

.inputContainer button:hover {
  background-color: #005bb5;
}

/* =======================
   SCROLL ESTILIZADO
   ======================= */
.userList::-webkit-scrollbar,
.messages::-webkit-scrollbar {
  width: 8px;
}

.userList::-webkit-scrollbar-thumb,
.messages::-webkit-scrollbar-thumb {
  background-color: var(--border-light);
  border-radius: 4px;
}

.darkTheme .userList::-webkit-scrollbar-thumb,
.darkTheme .messages::-webkit-scrollbar-thumb {
  background-color: var(--border-dark);
}

/* =======================
   TELAS SECUNDÁRIAS
   ======================= */
.profileScreen, .configScreen, .addContactScreen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  gap: 20px;
}

/* Campos e botões */
.centeredFieldset {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  padding: 20px;
  border: 1px solid #ccc;
  width: 320px;
  gap: 15px;
}

.inputField {
  width: 90%;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #aaa;
  font-size: 16px;
}

.roundedButton {
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: 0.3s;
}

.roundedButton:hover {
  background-color: #0056b3;
}

/* Botão Voltar */
.backButton {
  position: absolute;
  top: 10px;
  left: 10px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 20px;
  cursor: pointer;
  z-index: 30;
  transition: background 0.3s;
}

.backButton:hover {
  background: #0056b3;
}

.darkTheme .backButton {
  background: #333;
  color: white;
}

/* Logo */
.appLogo {
  text-align: center;
  font-size: 1.2em;
  font-weight: bold;
  color: var(--text-color, #333);
  margin-bottom: 10px;
}

.darkTheme .appLogo {
  color: #fff;
}
